body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f6f8fd;

}


.app-task-screen{
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  height: calc(100vh - 4.5rem);
  overflow: hidden;
}









.mytasks {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  position: relative;
}

.modal-container{
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  z-index: 1025;
  overflow: scroll;
  -webkit-animation-name: wrap-show;
          animation-name: wrap-show;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards
}
.confirm-container{
    position: absolute;
      left: 50%;
      top: 50%;
      right: auto;
      bottom: auto;
      padding:30px 0;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      max-width: 400px;
      min-height: 150px;
      max-height: 90%;
      width: 100%;
      box-shadow: rgba(0, 0, 0, 0.5) 0px 0px 10px;
      border-radius: 5px;
      -webkit-animation-name: wrap-content-show;
              animation-name: wrap-content-show;
      -webkit-animation-duration: 1s;
              animation-duration: 1s;
      -webkit-animation-fill-mode: forwards;
              animation-fill-mode: forwards;
      z-index: 1030;
  }
.alert-container{
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    z-index: 1035;
    -webkit-animation-name: wrap-show;
            animation-name: wrap-show;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards
  }
  @-webkit-keyframes wrap-show{
    from{background:none}
    to{background-color: rgba(0,0,0,0.5);}
  }
  @keyframes wrap-show{
    from{background:none}
    to{background-color: rgba(0,0,0,0.5);}
  }
  @-webkit-keyframes wrap-content-show{
    from{top: 0}
    to{top:50%}
  }
  @keyframes wrap-content-show{
    from{top: 0}
    to{top:50%}
  }
  
.alert-content{
    position: absolute;
    left: 50%;
    top: 50%;
    right: auto;
    bottom: auto; 
    -webkit-transform: translate(-50%, -50%); 
            transform: translate(-50%, -50%);
    width: 80%;
    border-radius: 5px;
    -webkit-animation-name: wrap-content-show;
            animation-name: wrap-content-show;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards
  }


.header-container{
