}
.app-header{
    -webkit-transition: .4s 0ms;
    transition: .4s 0ms;
    left: 0;
    position: fixed;
    top: 0;
    white-space: nowrap;
    width: 100%;
    z-index: 1000;
    box-shadow: 0 1px 0 0 rgba(0,0,0,.1);
    height:60px;
  }

